
type ButtonProps = {
  children: string;
  sx: React.CSSProperties
  handleClick: (event: React.MouseEvent<HTMLButtonElement>) => void
};

const Button = (props: ButtonProps) => {
  console.log("Style props: ", props.sx)
  return <button style={props.sx} onClick={(event) => props.handleClick(event)}>{props.children}</button>;
};
 
export default Button;

